@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100&display=swap);
.movies {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: flex-start;
    width: 80%;
    padding-top: 70px;
}

.movies_movie {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    width: 45%;
    margin-bottom: 70px;
    padding: 25px;
    background-color: white;
    box-shadow: 0 0 1em grey;
    border-radius: 10px;
}

.movies_movie img {
    position: relative;
    top: -50px;
    width: 140px;
}

.movie_data {
    padding-left: 20px;
    color: gray;
}

.movie_title {
    color: black;
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 10px;
}

.movie_year, .genres_genre, .movie_summary {
    font-size: 14px;
}

.genres {
    display: flex;
}

.genres_genre {
    padding: 0 10px 0 0;
}
* {
    font-family: 'Noto Sans KR', sans-serif;
    font-weight: 100;
    list-style: none;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    /* 테두리와 안쪽 여백의 크기도 요소의 크기로 고려 */
}

.main, html, body, .container {
    display: flex;
    justify-content: center;
    height: 100%;
}

body {
    background-color: #f2f2f2;
}

.loader {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    /* 가로 중앙 정렬 */
    align-items: center;
    /* 세로 중앙 정렬*/
    font-weight: bold;
}

/* 
display : flex; 일 때 적용

flex-wrap
- wrap : 기준 너비 안에 element들을 넣음 (overflow를 막음 → 여러줄)
- no-wrap : element들이 기준 너비를 넘어감 (overflow되도 한 줄로 배치)
- wrap-reverse : 오른쪽 위에서부터 왼쪽 아래로 정렬

justify-content (가로)
- flex-start : 왼쪽 정렬
- flex-end : 오른쪽 정렬
- center : 가운데 정렬
- space-around : 양옆의 margin의 너비가 같게 정렬
- space-between : 양쪽 정렬
- space-evenly : 빈 공간의 너비가 동일

align-items (세로)
- stretch : 기본
- flex-start : 위쪽 정렬
- flex-end : 아래쪽 정렬
- center : 중앙 정렬

*/
